import React, { useEffect } from 'react';
import { createPortal } from 'react-dom';
import './Modal.scss';
import IconButton from '../IconButton/IconButton';
import { ReactComponent as CloseIcon } from '../../icons/close.svg';

const modalRoot = document.querySelector('#modal-root');

// –ø–µ—Ä–µ–∏—Å–ø–æ–ª—å–∑—É–µ–º–∞—è –º–æ–¥–∞–ª–∫–∞, –∫–∞–∫ children –ª—é–±–æ–π –∫–æ–Ω—Ç–µ–Ω—Ç –ø–µ—Ä–µ–¥–∞–µ–º
export default function Modal({ onClose, children }) {
  useEffect(() => {
    // –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É —ç—Å–∫–ø–µ–π–ø–∞
    function handleKeyDown(e) {
      if (e.code === 'Escape') {
        onClose();
      }
    }
    window.addEventListener('keydown', handleKeyDown);

    return function clearup() {
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [onClose, children]);

  //   –∑–∞–∫—Ä—ã—Ç–∏–µ –ø–æ –∫–ª–∏–∫—É –≤ –±—ç–∫–¥—Ä–æ–ø
  function handelBackdropClick(e) {
    if (e.target === e.currentTarget) {
      onClose();
    }
  }

  // "–ø–æ—Ä—Ç–∞–ª" –¥–ª—è –º–æ–¥–∞–ª–∫–∏ (—É–±–∏—Ä–∞–µ—Ç –∫–æ—Å—Ç—ã–ª—å —Å z-index –∏ –æ–≤–µ—Ä—Ñ–ª–æ—É)
  return createPortal(
    <div className="Modal__backdrop" onClick={handelBackdropClick}>
      <div className="Modal__content">
        <IconButton
          className="Modal__close IconButton"
          aria-label="Close Modal icon"
          onClick={onClose}
        >
          <CloseIcon width="32" height="32" fill="#black" />
        </IconButton>
        {children}
      </div>
    </div>,
    modalRoot,
  );
}
